come from `src/styles/main.css` via Tailwind @theme.\n *\n *  Rizonetech (Pty) Ltd.  https://rizonesoft.com\n */\n\n/* ================================================================\n   Utility  hidden (Tailwind v4 may not scan .ts files for this)\n   ================================================================ */\n\n.hidden {\n  display: none !important;\n}\n\n/* ================================================================\n   Scrollbar  slim, theme-aware\n   ================================================================ */\n\n::-webkit-scrollbar {\n  width: 6px;\n}\n\n::-webkit-scrollbar-track {\n  background: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n  background: var(--color-glide-border);\n  border-radius: 3px;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background: var(--color-glide-text-tertiary);\n}\n\n/* ================================================================\n   Tab bar\n   ================================================================ */\n\n.glide-tab {\n  display: inline-flex;\n  align-items: center;\n  gap: var(--spacing-xs);\n  padding: var(--spacing-sm) var(--spacing-md);\n  font-family: var(--font-glide);\n  font-size: var(--text-sm);\n  font-weight: 500;\n  color: var(--color-glide-text-secondary);\n  background: transparent;\n  border: none;\n  border-bottom: 2px solid transparent;\n  cursor: pointer;\n  transition: color var(--duration-glide) var(--ease-glide),\n              border-color var(--duration-glide) var(--ease-glide);\n  white-space: nowrap;\n}\n\n.glide-tab:hover {\n  color: var(--color-glide-text);\n}\n\n.glide-tab--active {\n  color: var(--color-glide-blue);\n  border-bottom-color: var(--color-glide-blue);\n}\n\n.glide-tab--icon {\n  padding: var(--spacing-sm);\n  margin-left: auto;\n}\n\n.glide-tab--icon:first-of-type {\n  margin-left: auto;\n}\n\n/* ================================================================\n   Dropdown menu  proper menu component\n   ================================================================ */\n\n.glide-dropdown {\n  position: absolute;\n  right: 0;\n  top: calc(100% + 4px);\n  min-width: 200px;\n  background: var(--color-glide-bg-elevated);\n  border: 1px solid var(--color-glide-border);\n  border-radius: var(--radius-md);\n  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.10),\n              0 1px 4px rgba(0, 0, 0, 0.06);\n  padding: var(--spacing-xs) 0;\n  z-index: 20;\n  overflow: hidden;\n}\n\n.glide-dropdown__item {\n  display: flex;\n  align-items: center;\n  gap: var(--spacing-sm);\n  width: 100%;\n  padding: var(--spacing-sm) var(--spacing-md);\n  font-family: var(--font-glide);\n  font-size: var(--text-sm);\n  color: var(--color-glide-text);\n  background: transparent;\n  border: none;\n  cursor: pointer;\n  transition: background var(--duration-glide) var(--ease-glide);\n  text-align: left;\n}\n\n.glide-dropdown__item:hover {\n  background: var(--color-glide-bg-panel);\n}\n\n.glide-dropdown__item:active {\n  background: var(--color-glide-blue-light);\n}\n\n.glide-dropdown__item svg {\n  color: var(--color-glide-text-tertiary);\n  flex-shrink: 0;\n}\n\n.glide-dropdown__item:hover svg {\n  color: var(--color-glide-blue);\n}\n\n.glide-dropdown__item--active {\n  background: var(--color-glide-blue-light);\n  color: var(--color-glide-blue);\n  font-weight: 500;\n}\n\n.glide-dropdown__item--active svg {\n  color: var(--color-glide-blue);\n}\n\n.glide-split--open .glide-tab--icon {\n  color: var(--color-glide-blue);\n  background: var(--color-glide-blue-light);\n  border-radius: var(--radius-xs);\n}\n\n/* ================================================================\n   Buttons\n   ================================================================ */\n\n.glide-btn {\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  gap: var(--spacing-xs);\n  padding: 9px var(--spacing-lg);\n  border-radius: var(--radius-xs);\n  font-family: var(--font-glide);\n  font-size: var(--text-sm);\n  font-weight: 500;\n  cursor: pointer;\n  transition: all var(--duration-glide) var(--ease-glide);\n  white-space: nowrap;\n  border: 1px solid transparent;\n  line-height: 1;\n}\n\n.glide-btn:active {\n  transform: scale(0.97);\n}\n\n/* Primary  blue filled */\n.glide-btn--primary {\n  background: var(--color-glide-blue);\n  color: var(--color-glide-text-inverse);\n  border-color: var(--color-glide-blue);\n}\n\n.glide-btn--primary:hover {\n  background: var(--color-glide-blue-hover);\n  border-color: var(--color-glide-blue-hover);\n  box-shadow: 0 2px 8px rgba(59, 158, 255, 0.25);\n}\n\n/* Secondary  outlined */\n.glide-btn--secondary {\n  background: var(--color-glide-bg);\n  color: var(--color-glide-text-secondary);\n  border-color: var(--color-glide-border);\n}\n\n.glide-btn--secondary:hover {\n  border-color: var(--color-glide-text-secondary);\n  color: var(--color-glide-text);\n  background: var(--color-glide-bg-panel);\n}\n\n/* Accent  purple filled */\n.glide-btn--accent {\n  background: var(--color-glide-purple);\n  color: var(--color-glide-text-inverse);\n  border-color: var(--color-glide-purple);\n  padding: 9px var(--spacing-sm);\n}\n\n.glide-btn--accent:hover {\n  background: var(--color-glide-purple-hover);\n  border-color: var(--color-glide-purple-hover);\n}\n\n/* Success state (JS-toggled after copy actions) */\n.glide-btn--success {\n  background: var(--color-glide-success) !important;\n  border-color: var(--color-glide-success) !important;\n  color: var(--color-glide-text-inverse) !important;\n}\n\n/* Icon-only toggle (e.g. API key show/hide) */\n.glide-btn--icon-toggle {\n  padding: 8px 10px;\n}\n\n/* ================================================================\n   Form controls  shared styles for select, input, textarea\n   ================================================================ */\n\n.glide-select {\n  display: block;\n  width: 100%;\n  padding: var(--spacing-sm);\n  font-family: var(--font-glide);\n  font-size: var(--text-md);\n  color: var(--color-glide-text);\n  b