t-sm);\n  color: var(--color-glide-text);\n}\n\n.glide-radio input[type=\"radio\"] {\n  appearance: none;\n  -webkit-appearance: none;\n  width: 14px;\n  height: 14px;\n  border: 1.5px solid var(--color-glide-border);\n  border-radius: 50%;\n  margin: 0;\n  cursor: pointer;\n  position: relative;\n  flex-shrink: 0;\n  transition: border-color var(--duration-glide) var(--ease-glide);\n}\n\n.glide-radio input[type=\"radio\"]:hover {\n  border-color: var(--color-glide-blue);\n}\n\n.glide-radio input[type=\"radio\"]:checked {\n  border-color: var(--color-glide-blue);\n}\n\n.glide-radio input[type=\"radio\"]:checked::after {\n  content: '';\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 6px;\n  height: 6px;\n  border-radius: 50%;\n  background: var(--color-glide-blue);\n}\n\n/* ================================================================\n   Checkbox\n   ================================================================ */\n\n.glide-checkbox {\n  display: inline-flex;\n  align-items: center;\n  gap: 6px;\n  cursor: pointer;\n  font-size: var(--text-sm);\n  color: var(--color-glide-text);\n}\n\n.glide-checkbox input[type=\"checkbox\"] {\n  appearance: none;\n  -webkit-appearance: none;\n  width: 14px;\n  height: 14px;\n  border: 1.5px solid var(--color-glide-border);\n  border-radius: 3px;\n  margin: 0;\n  cursor: pointer;\n  position: relative;\n  flex-shrink: 0;\n  transition: all var(--duration-glide) var(--ease-glide);\n}\n\n.glide-checkbox input[type=\"checkbox\"]:hover {\n  border-color: var(--color-glide-blue);\n}\n\n.glide-checkbox input[type=\"checkbox\"]:checked {\n  background: var(--color-glide-blue);\n  border-color: var(--color-glide-blue);\n}\n\n.glide-checkbox input[type=\"checkbox\"]:checked::after {\n  content: '';\n  position: absolute;\n  top: 1px;\n  left: 4px;\n  width: 4px;\n  height: 8px;\n  border: solid white;\n  border-width: 0 2px 2px 0;\n  transform: rotate(45deg);\n}\n\n/* ==================